.about_section{
    padding-top: 125px;

    background-color: #FFF5F6;
    ;

    @media screen and (min-width: 768px) and (max-width: 1279px) {
        padding-top: 130px;
    }
}
.about_title-conteiner{
    display: flex;
    flex-direction: column-reverse;

    .about_subtitle{
        @extend %pre-title;

        margin-bottom: 15px;

        @media screen and (min-width: 768px) and (max-width: 1279px) {
            margin-bottom: 16px;
        }
    }

    .about_title{
        @extend %section-title;

        margin-bottom: 26px;

        @media screen and (min-width: 768px) {
            margin-bottom: 50px;
        }

        @media screen and (min-width: 1280px) {
            margin-bottom: 55px;
        }
    }
}

.about_bagroun-image{
    @media screen and (min-width: 1280px) {
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        background-image: url(../images/baground-milk-desktop.png);
    }
}

.about_description{
    @media screen and (min-width: 768px) {
        display: flex;
    }


}

.about_picture{

    @media screen and (max-width: 767px) {
        margin-bottom: 46px;
    }

    @media screen and (min-width: 768px) {
        margin-right: 17px;
    }

}


.about_text{
    margin: 0px auto;
    text-align: start;
    align-items: center;

    font-size: 12px;
    line-height: 176%;

    @media screen and (min-width: 768px) {
        width: 340px;

        font-size: 14px;
    }

    @media screen and (min-width: 1280px) {
        width: 463px;

        font-size: 14px;
    }

    .about_text-gray{
        color: #907E82;
        ;
    }

    .about_text-black{
        @media screen and (min-width: 1280px) {
            font-size: 16px;
        }
    }
}

.about_button{
    width: 176px;
    height: 44px;
    margin-top: 17px;
    padding: 0px 37px;

    font-weight: 700;
    font-size: 16px;

    color: #D41443;

    background-color: #fff;
    border-radius: 22px;

    @media screen and (min-width: 768px) {
        margin-top: 62px;
    }

    &:hover,
    &:focus{
        background-color: #ECEBEB;
    }
}
    
.about_button-icon{
    margin-left: auto;
    fill: currentColor
}