.social {
    margin-bottom: 46px;
    display: flex;

    @media screen and (min-width: $width-desktop) {
        margin-bottom: 50px;
    }
    
    &__icon {
        width: 70%;
        height: 70%;
        fill: currentColor;

    }

    &__link {
        
        display: flex;
        border-radius: 50%;
        width: 32px;
        height: 32px;
        align-items: center;
        justify-content: center;
        background-color: $footer-icons-color;

        color: $white-text-color;
            
       transition: background-color $primari-trasition-duration cubic-bezier($cubic-bezier);

       &:hover, 
       &:focus {
          background-color: $accent-color;
        }
    }   





    &__item + &__item {
        margin-left: 28px;
    }



}







       
